html, body {
  /* height: 100%; */
  width: 100%;
  margin: 0;
}

input {
  color: white !important;
}

input::-webkit-input-placeholder {
  color: white !important;
}

input:-ms-input-placeholder {
  color: white !important;
}

input::-ms-input-placeholder {
  color: white !important;
}

input::placeholder {
  color: white !important;
}

.sections {
  /* height: 100%; */
}

.landing-section {
  width: 100%;
  height: 100vh;
  -webkit-transition: background-color 0.8s ease-out;
  -o-transition: background-color 0.8s ease-out;
  transition: background-color 0.8s ease-out;
}


.row-margin-bottom {
  margin-bottom: 10px;
}

.content-text-title {
  font-size: 20px;
}

.content-text {
  color: white;
  font-size: 15px;
}

@media (min-width: 768px) {

.content-text {
  font-size: 20px;
}

.content-text-title {
  font-size: 30px;
}

}

img.download-badge {
  height: 40px;
}



