.vertical-navigation-stepper-bar {
  position: fixed;
}

@media(min-width: 992px) {

.vertical-navigation-stepper-bar {
  top: 94px;
  left: calc(50% - 470px);
}

}

@media(min-width: 1200px) {

.vertical-navigation-stepper-bar {
  top: 114px;
  left: calc(50% - 600px + 63px);
}

}

.navigation-item {
  font-size: 12px;
  text-align: center;
  color: #FFF;
  opacity: 0.4;
  -webkit-transition: all 0.3s linear;
  -o-transition: all 0.3s linear;
  transition: all 0.3s linear;
}

.navigation-item-active {
  opacity: 1;
}

.navigation-item-hover {
  opacity: 0.7;
}

.navigation-link {
  color: white;
  text-decoration: none;
  cursor: pointer;
}

a.navigation-link:hover {
  text-decoration: none;
  color: white;
  cursor: pointer;
}

.navigation-link-img {
  height: 42px;
}

.navigation-link-text {
  margin-top: 5px;
  display: block;
  word-wrap: break-word;
  font-weight: 600;
  width: 100px;
}

.navigation-link-border {
  opacity: 1 !important;
  margin-bottom: 15px;
  margin-top: 5px;
  width: 1px;
  height: 20px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  position: relative;
  left: 50%;
}



