.link-next-slide {
  display: block;
  bottom: 20px;
  left: calc(50% - 25px);
  text-align: center;
  font-size: 14px;
  position: fixed;
  color: White;
  opacity: 0.5;
  -webkit-transition: opacity .2s ease-out;
  -o-transition: opacity .2s ease-out;
  transition: opacity .2s ease-out;
}

.link-next-slide:hover {
  opacity: 1;
  text-decoration: none;
  cursor: pointer;
}



